*{
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body{
  font-family: 'Roboto', sans-serif;
  margin: 0;

}
h1{ font-size: 3.5em;}
h2{ font-size: 2.7em;}
h3{ font-size: 2em;}
p{ font-size: 1.25em;}
ul{ list-style: none;}
button{
  font-size: 1.5em;
  font-weight: bold;
  padding: 10px 30px;
  border-radius: 10px;
  border: 2px solid rgba(0,0,0,0.5);
  background-color: rgba(226, 53, 53, 0.948);


}

button:hover{
  background-color: rgba(201, 31, 31, 0.867);
}

.container{
  max-width: 1300px;
  margin: auto;
}

header{
  background-color: rgb(245,245,245);

}
header .logo{
  margin: 0;
  padding: 25px 30px;
  font-weight: bold;
  color:rgb(255, 39, 39);
  font-size: 1.6em;
}
header .container{
  display:flex;
  flex-direction: column;
  align-items: center;
}
header nav{
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-bottom: 25px;
}
header a{
  padding: 5px 12px;
  text-decoration: none;
  font-weight: bold;
  color: black;
  
}
header a:hover{
  color: rgb(255, 39, 39)
}

#banner{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  height: 90vh;
  background-image: linear-gradient(
      0deg,
      rgba(0,0,0,0.5),
      rgba(0,0,0,0.5)
  )
  ,url("Assets/imgs/banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: rgb(245,245,245);

}
#cartelera{
  background-color: rgb(30, 30,30);
  color: white;
  text-align: center;
}
#cartelera .container{
  padding: 100px 0px;
}
#cartelera p{
  display: none;
}
#cartelera h2{
  margin-top: 0;
  font-size: 3.2em;
}
#cartelera .carta{
  background-position: center center;
  background-size: cover;
  padding: 50px;
  margin: 20px;
}
.carta:first-child{
  background-image: linear-gradient(
      0deg,
      rgba(0,0,0,0.5),
      rgba(0,0,0,0.5)
  )
  ,url("Assets/imgs/costumbrista.jpg");
  
}
.carta:nth-child(2){
  background-image: linear-gradient(
      0deg,
      rgba(0,0,0,0.5),
      rgba(0,0,0,0.5)
  )
  ,url("Assets/imgs/tambo.jpg");
  
}
.carta:nth-child(3){
  background-image: linear-gradient(
      0deg,
      rgba(0,0,0,0.5),
      rgba(0,0,0,0.5)
  )
  ,url("Assets/imgs/musef.jpg");
  
}
#segunda-seccion{
  height: 80vh;
  background-image: linear-gradient(
      0deg,
      rgba(0,0,0,0.5),
      rgb(0, 0, 0,0.5)
  )
  ,url("Assets/imgs/museos2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: rgb(245,245,245);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;

}
#segunda-seccion h2{
  margin-top: 0%;
  
  font-size: 3.2em;
}
 #segunda-seccion .carrusel{
  display: flex;
  width : 600 px;
  height: 430px;
}


.carrusel img{
      width:150px;
      flex-grow: 1;
      object-fit: cover;
      opacity: .8;
      transition: .5s ease;
  }

 .carrusel img:hover {
      cursor: crosshair;
      width: 400px;
      opacity: 1;
      filter: contrast(120%);
  }  




#segunda-seccion .container{
  text-align: center;
  padding: 60px 12px;
}
#segunda-seccion li{
  margin: 16px 0px;
  font-weight: bold;
  font-size: 1.3em;
}
footer .container{
  height: 50;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
@media (min-width: 720px){
  header .container{
      flex-direction: row;
      justify-content: space-between;
  }
  header nav{
      flex-direction: row;
      padding-bottom: 0;
      padding-right: 20px

  }

  #cartelera .pelis{
      display: flex;
      justify-content: center;

  }
  #cartelera p {
      display: block;
      margin-bottom: 30px;
  }
  #cartelera h3{
      margin-top: 0;
  }
  #cartelera .carta{
      padding: 50px;
  }
  footer .container{
      justify-content: flex-end;
  }
}
.enlaces {
  margin-top: 10px;
  font-size: 150%;
  text-align: center;
}
.enlaces a{
  display: block;
  text-decoration: none;
  color: rgb(245,245,245);
  margin-bottom: 3px;
}
.enlaces a:hover{
  color: rgba(201, 31, 31, 0.867);
}